 .mainCarouselDiv {
  width: 100%;
  height: 500px;
  /* border: 2px solid green; */
  /* background-color: green; */
  position: relative;
  margin-bottom: 2%;
}

.sliderDiv{
    width: 98%;
    height: 400px;
    position: absolute;
    transform: translate(-50%,-50%);
    top: 53%;
    left: 50%;
    margin-top: -3%;
    /* border: 2px solid gold; */
}
.div3d{
    background-color: blueviolet;
    width: 300px;
    /* height: 400px; */
    /* margin: 10px; */
}

.populardestinationsslide{
    width: 105% !important;
    /* filter: brightness(0.8); */
    height: 410px;
    /* border: 2px solid red; */
    transform: scale(0.8);
    transition: .7s;
    /* filter: blur(2px); */
    border-radius: 20px;
}

.populardestinationsslide>img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 20px;
    /* filter: blur(2px); */
    /* margin-left: 5%; */
}
.populardestinationsslide>h5{
    padding: 5px;
    text-align: center;
    color: white;
    font-weight: 700;
    font-size: 25px;
    margin-top: 2%;
}

.populardestinationsslide>p{
  padding: 5px;
  text-align: center;
  color: white;
  font-weight: 700;
  font-size: 15px;
  margin-top: -87%;
}

.populardestinationsslide-active{
    width: 105% !important;
    height: 420px;
    filter: blur(0);
    opacity: 1;
    transform: scale(1);
    /* border: 2px solid red; */
    border-radius: 20px;
}
